  .home-tab::before {
    content: "";
    position: absolute;
    height: 2px;
    bottom: 24px;
    background-color: #ffac07;
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
  }
  .underscore{
    position: absolute;
    height: 2px;
    bottom: 24px;
    left: 80px;
    background-color: #ffac07;
    visibility: hidden;
    transform: scaleX(0);
    width: 50px;
  }
.home-underscore{
