/* Large devices (desktops, 992px and up) */

  .header-logo{
    max-width: 72px;
    margin-top: 15px;
  }
  .header{
    background-color: #ffff;
    height: 105px;
    width: auto;
  }
  .header-scroll{
    box-shadow: 0 6px 11px 0 rgba(172, 172, 201, 0.5);
  }
  .tabs{
    margin: auto;
    float: none;
    text-align: center;
    margin-top: 35px !important;
  }
  .tabs a{
    color: #122025;
    font-weight: 400;
    font-family: arimo !important;
    padding: 0px 40px 0px 10px;
    cursor: pointer;
    /* color: #ffac07; */
  }
 .home-tab:hover {
    color: #ffac07 !important;
  }
  .home-tab::before {
    content: "";
    position: absolute;
    height: 2px;
    bottom: 24px;
    background-color: #ffac07;
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
  }
  .underscore{
    position: absolute;
    height: 2px;
    bottom: 24px;
    left: 80px;
    background-color: #ffac07;
    visibility: hidden;
    transform: scaleX(0);
    width: 50px;
  }
.home-underscore{
    position: absolute;
    height: 2px;
    bottom: 24px;
    background-color: #ffac07;
    visibility: visible;
    width: 50px;
}
.about-underscore{
  position: absolute;
  height: 2px;
  bottom: 24px;
  background-color: #ffac07;
  visibility: visible;
  width: 85px;
}
.project-underscore{
  position: absolute;
  height: 2px;
  bottom: 24px;
  background-color: #ffac07;
  visibility: visible;
  width: 155px;
}
.service-underscore{
  position: absolute;
  height: 2px;
  bottom: 24px;
  background-color: #ffac07;
  visibility: visible;
  width: 128px;
}
.gallery-underscore{
  position: absolute;
  height: 2px;
  bottom: 24px;
  background-color: #ffac07;
  visibility: visible;
  width: 80px;
}
.contact-underscore{
  position: absolute;
  height: 2px;
  bottom: 24px;
  background-color: #ffac07;
  visibility: visible;
  width: 108px;
}
  .home-tab:hover::before {
    visibility: visible;
    transform: scaleX(1);
  }

  .underline-home::before{
    width: 50px;
  }
  .underline-about::before{
    width: 82px;
  }
  .underline-project::before{
    width: 150px;
  }
  .underline-service::before{
    width: 122px;
  }
  .underline-gallery::before{
    width: 75px;
  }
  .underline-contact::before{
    width: 105px;
  }
  .custom-padding{
    padding: 0px 10px 0px 10px;
  }

